.switch{position:absolute;width:50px;height:100%;top:0;left:50%;transform:translateX(-50%);cursor:pointer;z-index:2}.switch::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-image:var(--image_theme-switch);background-size:contain;background-repeat:no-repeat;z-index:2}.switch:hover::after{content:"";position:absolute;width:0px;height:0px;border-radius:50%;background-color:var(--color_f-bg-theme-switcher);top:50%;left:50%;transform:translate(-50%, -50%);box-shadow:var(--shadow_theme-switcher);z-index:1}